<script>
	export let name;
	export let place;
	export let dataset;
	export let value;
	export let total;
	export let unit;
	
	let set = unit === '%' ? 'perc' : 'val';
	
	let breaks = [
		Math.round(total / 5),
		Math.round((2 * total) / 5),
		Math.round((3 * total) / 5),
		Math.round((4 * total) / 5),
	];
	
	let rank = place.data[dataset].rank.change[value];
	let quintile = rank < breaks[0] ? 'q5' : rank < breaks[1] ? 'q4' : rank < breaks[2] ? 'q3' : rank < breaks[3] ? 'q2' : 'q1';
</script>

<style>
	.muted {
		color: #555;
	}
	.positive {
		color: green;
	}
	.negative {
		color: red;
	}
	.q1 {
		color: #d7191c;
	}
	.q2 {
		color: #fdae61;
	}
	.q3 {
		color: #ffffbf;
	}
	.q4 {
		color: #a6d96a;
	}
	.q5 {
		color: #1a9641;
	}
</style>

<li>{name}: <strong>{place.data[dataset][set].c2011[value].toLocaleString()}{unit}</strong> 
{#if place.data[dataset][set].change[value] < 0}
  <span class="muted ">({place.data[dataset][set].change[value]}%)</span>
{:else if place.data[dataset][set].change[value] > 0}
  <span class="muted ">(+{place.data[dataset][set].change[value]}%)</span>
{:else}
<span class="muted ">({place.data[dataset][set].change[value]}%)</span>
{/if}
<small class="{quintile}">{place.data[dataset].rank.change[value]} of {total}</small>			</li>
